/* Header */

.header {
	padding: 40px 0;
	min-height: 695px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--header-bg);
	/* background-color: #345466; */
	background-image: url('./../../img/header-bg.png');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: center center;
	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: var(--accent);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}


@media (max-width: 380px)  {
	.header__title {
		font-size: 36px;
	}
	.header__title strong {
		font-size: 48px;
	}
	.header__text {
		font-size: 17px;
	}
	.header {
		padding: 20px 0;
		min-height: 630px;
	}
}